<template>
  <div class="deposit-page-step-1">
    <DepositPageInfo />
    <Input v-model="email" :placeholder="emailPlaceholder" class="deposit-page-step-1__email">
      <template #prepend>
        <Icon class="deposit-page-step-1__email-notify-bell" name="notify-bell" />
      </template>
      <template #append>
        <Button class="deposit-page-step-1__email__button" type="primary" @click="processDeposit">
          <Icon name="arrow-right-full" />
        </Button>
      </template>
    </Input>
  </div>
</template>

<script>
import { MODULES_NAMES } from '@/shared/common/modules'
import { useActions, useGetters, useMutations } from '@/shared/plugins/vuex'
import { computed, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import Button from '@/shared/components/UIKit/Button'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'
import DepositPageInfo from '@/client/views/DepositPage/DepositPageInfo'

export default {
  name: 'Step1',
  components: {
    DepositPageInfo,
    Button,
    Input,
    Icon
  },
  emits: ['next'],
  setup (_, { emit }) {
    const { t, locale } = useI18n()
    const {
      SET_TOOLS: setTools
    } = useMutations(MODULES_NAMES.TOOLS)
    const {
      setEmail
    } = useActions(MODULES_NAMES.EXCHANGE)
    const {
      getEmail,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)

    const coinAmount = ref(20.62151445)
    const code = ref('MVK89JBNqZQv5zMAKG8FK3kJM4rjt')
    const emailPlaceholder = ref('notifyme@myemail.com')

    const email = computed({
      get: () => getEmail.value,
      set: setEmail
    })

    const processDeposit = () => {
      emit('next', 1)
    }

    setTools([
      {
        icon: {
          name: 'copy',
          width: '20px',
          height: '20px'
        },
        copyUrl: true
      }
    ])

    return {
      t,
      email,
      emailPlaceholder,
      locale,
      sendCoin,
      code,
      coinAmount,
      processDeposit
    }
  }
}
</script>

